<template>
    <div class="bottom-nav">
        <mt-tabbar v-model="selected" fixed>
            <mt-tab-item id="0">
                <img slot="icon" v-if="selected == '0'" :src="navResumeActive">
                <img slot="icon" v-else :src="navResume">
                <span :class="{activeSpan:selected=='0' }">人才管理</span>
            </mt-tab-item>
            <mt-tab-item id="1">
                <img slot="icon" v-if="selected == '1'" :src="navCustomerActive">
                <img slot="icon" v-else :src="navCustomer">
                <span :class="{activeSpan:selected=='1' }">客户</span>
            </mt-tab-item>
            <mt-tab-item id="2"  @click.native="addCustomerLoading">
                <!-- <img slot="icon" class="nav-add" :src="navAdd"> -->
                <span :class="{activeSpan:selected=='2' }" class="middle-font"></span>
            </mt-tab-item>
            <mt-tab-item id="3">
                <img slot="icon" v-if="selected == '3'" :src="navPostIconActive">
                <img slot="icon" v-else :src="navPostion">
                <span :class="{activeSpan:selected=='3' }">职位</span>
            </mt-tab-item>
            <mt-tab-item id="4" @click.native="toggle">
                <img slot="icon" v-if="selected == '4'" :src="navPersonActive">
                <img slot="icon" v-else :src="navPerson">
                <span :class="{activeSpan:selected=='4' }">个人中心</span>
            </mt-tab-item>
        </mt-tabbar>
        <div class="zhanwei"></div>
        <div class="customer-post" v-show="addCustomerPostPopup">
            <div class="content">
                <a href="javscript:;" class="add-customer"  @click="addCustomerBtn">
                    <img :src="addCustomer" />
                    <p>新增客户</p>
                </a>
                 <a href="javscript:;" class="add-post" @click="releaseNewPost">
                    <img :src="addPostIcon" />
                    <p>发布职位</p>
                </a>
            </div>
            <a href="javascript:;" class="close-customer-post" @click="closeCusPost">
                <img :src="closeCustomerPost" />
            </a>
        </div>
  </div>
  
</template>
<script>
import Util from '@/js/mUtils';
import navResume from "@/assets/qds-img/nav-resume.png";
import navResumeActive from "@/assets/qds-img/nav-resume-active.png";
import navCustomer from "@/assets/qds-img/nav_customer.png"
import navCustomerActive from "@/assets/qds-img/nav_customer_active.png"
import navPostion from "@/assets/qds-img/nav_post.png"
import navPostIconActive from "@/assets/qds-img/nav_post_active.png"
import navPerson from "@/assets/qds-img/nav_person.png"
import navPersonActive from "@/assets/qds-img/nav_person_active.png"
import navAdd from "@/assets/qds-img/nav_add.png"

import addPostIcon from "@/assets/qds-img/add_post_icon.png"
import addCustomer from "@/assets/qds-img/add_customer_icon.png"
import closeCustomerPost from "@/assets/qds-img/close_customer_post_icon.png"

export default {
    props: ['fatherActive','fatherIntegration'],
     data() {
        return {
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            selected: '0',
            navResume: navResume,
            navResumeActive: navResumeActive,
            navCustomer: navCustomer,
            navCustomerActive: navCustomerActive,
            navPostion: navPostion,
            navPostIconActive: navPostIconActive,
            navPerson: navPerson,
            navPersonActive: navPersonActive,
            navAdd: navAdd,
            addCustomerPostPopup: false,
            addPostIcon: addPostIcon,
            addCustomer: addCustomer,
            closeCustomerPost: closeCustomerPost,
            certificationStatus: '',
        }
    },
    mounted() {
        this.selected = this.fatherActive
        // 添加请求拦截器
        var token = Util.getCookie('qdsMToken');
        this.axios.interceptors.request.use(function (config) {
            // 在发送请求之前做些什么
            if(token == null || token == "" || token == undefined) {
                document.cookie = "qdsMToken" + "=" + '' + ";path=/" 
                this.$router.push('/');
            }
            return config;
        }, function (error) {
        // 对请求错误做些什么
            return Promise.reject(error);
        });
        var that = this
        that.certificationStatus = sessionStorage.getItem("qdsMCertificationStatus")            //从sessionStorage中拿渠道商认证状态值
        that.getUserAuthStatus = setInterval(() => {
            if(that.certificationStatus < 3 || that.certificationStatus == null || that.certificationStatus == '' || that.certificationStatus == undefined) {
                that.axios(that.qdsGlobalApiUrl + 'getChannelUser?token=' + token, {
                    method: 'POST',
                })
                .then(function(response){
                    if(response.data.entity.certificationStatus) {
                        sessionStorage.setItem('qdsMCertificationStatus',response.data.entity.certificationStatus)             //认证状态数字
                        that.certificationStatus = sessionStorage.getItem("qdsMCertificationStatus")                      //从sessionStorage中拿认证状态值
                    }
                })
                .catch(function(error){
                })
            }
        }, 5000)
    },
    beforeDestroy () {
        clearInterval(this.getUserAuthStatus)
    },
    methods: {
        addCustomerLoading() {
            this.addCustomerPostPopup = true;
        },
        toggle() {
            this.$emit('repeatData',false)
        },
        goResume() {
            alert(1)
            this.$router.push('/qdsSearchResume')
        },
        goCustomer() {
            this.$router.push('/qdsCustomerManager')
        },
        toggleTab(index) {
        },
        closeCusPost() {
            this.addCustomerPostPopup = false
        },
        addCustomerBtn() {
            localStorage.setItem('qdsMcustomerCont','')   // 标记从编辑页面进入的
            if(this.certificationStatus == 1)  {
                this.$router.push('/qdsAttestation');
            }else if(this.certificationStatus == 2) {
                if(this.$route.path == '/qdsPersonal') {
                    this.addCustomerPostPopup = false;
                }else{
                    this.$router.push('/qdsPersonal');
                } 
            }else if(this.certificationStatus == 3) {
                this.$router.push('/qdsNewCustomer');
            }else if(this.certificationStatus == 4) {
                this.$router.push('/qdsAttestation');
            }
        },
        releaseNewPost() {
            if(this.certificationStatus == 1)  {
                this.$router.push('/qdsAttestation');
            }else if(this.certificationStatus == 2) {
                if(this.$route.path == '/qdsPersonal') {
                    this.addCustomerPostPopup = false;
                }else{
                    this.$router.push('/qdsPersonal');
                }
            }else if(this.certificationStatus == 3) {
                this.$router.push('/qdsReleaseNewPost');
            }else if(this.certificationStatus == 4) {
                this.$router.push('/qdsAttestation');
            }
        }
    },
    watch: {
        selected: function (val, oldVal) {
            // 这里就可以通过 val 的值变更来确定去向
            switch(val){
                case '0':
                    this.$router.push('/qdsSearchResume');
                break;
                case '1':
                    this.$router.push('/qdsCustomerManager');
                break;
                case '2':
                    this.addCustomerPostPopup = true;
                break;
                case '3':
                    this.$router.push('/qdsPostList');
                break;
                case '4':
                    this.$router.push('/qdsPersonal');
                    
                break;
            }
        },
        // fatherShow: function (val, oldVal) {
        //     // 这里就可以通过 val 的值变更来确定去向
        //     switch(val){
        //         case true:
        //             this.$router.push('/qdsPersonal');
        //             this.$emit('repeatData',false)
        //         break;
        //         case false:
        //             this.$router.push('/qdsPersonal');
        //         break;
        //     }
        // }
    },

}
</script>

<style scoped>
    .bottom-nav{
        position: relative;  
    }
    .zhanwei {
        height: 3.4375rem;
    }
    .customer-post{
        position: fixed;
        z-index: 1111;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transition: opacity .3s;
        transition: opacity .3s;
        background-color: rgba(255, 255, 255, 0.7);
    }
    .customer-post .content{
        position: relative;
        /* margin-top: 100%;
        display: flex;
        display: -webkit-flex;
        padding: 0 5.125rem;
        justify-content: space-between; */
    }
    .customer-post .content .add-customer{
        position: fixed;
        bottom: 7.5rem;
        left: 6.125rem;
        font-size: .875rem;
        color: #0B231F;
    }
    .add-customer img{
        display: inline-block;
        width: 3.4375rem;
        height: 3.4375rem;
    }
    .add-customer p{
        padding-top: .625rem;
    }
    .customer-post .content .add-post{
        position: fixed;
        bottom: 7.5rem;
        right: 6.125rem;
        font-size: .875rem;
        color: #0B231F;
    }
    .add-post img{
        display: inline-block;
        width: 3.4375rem;
        height: 3.4375rem;
    }
    .add-post p{
        padding-top: .625rem;
    }
    .close-customer-post{
        position: fixed;
        bottom: 1rem;
        left: 10.625rem;
        display: inline-block;
        width: 2.1875rem;
        height: 2.1875rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .close-customer-post img{
        width: .8125rem;
        height: .8125rem;
    }
    .bottom-nav .activeSpan{
        color: #2da1a4;
    }
    .nav-add{
        position: absolute;
        top: -1rem;
        left: -0.375rem;
        width: 2.1875rem;
        height: 2.1875rem;
    }
    .middle-font{
        position: relative;
        top: 1.875rem;
    }
    @media screen and (max-width: 320px) {
        .nav-add{
            position: absolute;
            width: 2.1875rem;
            top: -1rem;
            left: -0.25rem;
            height: 2.1875rem;
        }
        .close-customer-post{
            position: fixed;
            bottom: 2.2rem;
            left: 10.55rem;
            display: inline-block;
            width: 2.1875rem;
            height: 2.1875rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
</style>




